@font-face {
  font-family: 'Golos Text';
  src: local('Golos Text Regular'), local('Golos-Text-Regular'),
    url('../fonts/GolosText/GolosText-Regular.woff2') format('woff2'),
    url('../fonts/GolosText/GolosText-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Golos Text';
  src: local('Golos Text Demi Bold'), local('Golos-Text-Demi-Bold'),
    url('../fonts/GolosText/GolosText-DemiBold.woff2') format('woff2'),
    url('../fonts/GolosText/GolosText-DemiBold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}