@import "main-bx";

.nav {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 0 16px;
  @media (max-width: 700px) {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 17px 0;
  }
  &__title {
    color: $color_gray;
    margin-bottom: 16px;
    @media (max-width: 1200px) {
      margin-bottom: 20px;
    }
  }
  &__list {
    @media (max-width: 700px) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 0 7px;
    }
    li {
      letter-spacing: -.01em;
      &:not(:last-child) {
        margin-bottom: 8px;
        @media (max-width: 1200px) {
          margin-bottom: 16px;
        }
      }
    }
  }
}