@mixin golos {
  font-family: 'Golos Text', sans-serif;
}

@mixin cover {
  height: 100%;
  object-fit: cover;
}

@mixin contain {
  height: 100%;
  object-fit: contain;
}

@mixin grid {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 0 16px;
  @media (max-width: 1200px) {
    grid-template-columns: repeat(4, 1fr);
  }
  @media (max-width: 700px) {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 0;
  }
}