@import "main-bx";

.footer {
  padding: 42px 0;
  background-color: $color_bg;
  color: $color_white;
  @media (max-width: 1200px) {
    font-size: 12px;
    padding-bottom: 24px;
  }
  @media (max-width: 700px) {
    padding: 32px 0;
  }
  .logo {
    grid-column: span 2;
    width: max-content;
  }
  &__top {
    @include grid;
    align-items: center;
    margin-bottom: 32px;
    @media (max-width: 700px) {
      margin-bottom: 35px;
    }
    .footer__contacts {
      grid-column: span 2;
      margin: auto 0 2px;
      @media (max-width: 1200px) {
        display: none;
      }
      &-item {
        display: block;
        &:not(:last-child) {
          margin-right: 32px;
        }
      }
      &-phone {
        &s {
          display: inline-flex;
          margin-bottom: 8px;
        }
      }
      &-text {
        span {
          &:not(.shown) {
            display: initial;
          }
          &.shown {
            display: none;
          }
        }
      }
    }
  }
  &__button {
    grid-column: span 2;
    margin-left: auto;
    @media (max-width: 1200px) {
      display: none;
    }
  }
  &__content {
    @include grid;
    margin-bottom: 62px;
    @media (max-width: 1200px) {
      margin-bottom: 24px;
    }
    .nav {
      grid-column: span 4;
      @media (max-width: 1200px) {
        margin-bottom: 33px;
      }
      @media (max-width: 700px) {
        grid-column: span 1;
        margin-bottom: 24px;
      }
    }
  }
  &__list {
    @media (max-width: 1200px) {
      grid-column: span 4;
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-gap: 0 16px;
      margin-bottom: 34px;
      li {
        font-size: 16px;
        &:not(:last-child) {
          margin-bottom: 0;
        }
      }
    }
    @media (max-width: 700px) {
      grid-column: span 1;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 16px 7px;
      margin-bottom: 32px;
    }
    li {
      font-size: 20px;
      font-weight: 600;
      line-height: 1.2;
      letter-spacing: -.02em;
      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }
  &__info {
    display: flex;
    flex-direction: column;
    @media (max-width: 1200px) {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-gap: 0 16px;
      grid-column: span 4;
      border-top: 1px solid $color_gray;
      padding-top: 19px;
    }
    @media (max-width: 700px) {
      grid-column: span 1;
      grid-template-columns: repeat(1, 1fr);
      border: none;
      padding-top: 0;
    }
    .social {
      margin: auto 0;
      @media (max-width: 1200px) {
        margin: -4px 0 0;
      }
      @media (max-width: 700px) {
        margin: -0;
      }
    }
    .footer__button {
      display: none;
      @media (max-width: 1200px) {
        display: inline-flex;
        grid-column: span 4;
        margin: 32px 0 24px;
      }
      @media (max-width: 700px) {
        grid-column: span 1;
        margin: 24px 0;
      }
    }
  }
  &__contacts {
    margin-bottom: 5px;
    @media (max-width: 1200px) {
      grid-column: span 3;
      margin: 0;
    }
    @media (max-width: 700px) {
      grid-column: span 1;
      margin-bottom: 16px;
    }
    &-item {
      display: none;
      @media (max-width: 1200px) {
        display: block;
        padding-right: 10px;
      }
      @media (max-width: 700px) {
        padding-right: 0;
      }
      &.shown {
        display: block;
        .footer__contacts-phone {
          display: none;
          @media (max-width: 1200px) {
            display: inline-block;
            margin-bottom: 12px;
          }
        }
      }
    }
    &-phone {
      display: inline-block;
      font-size: 20px;
      font-weight: 600;
      color: $color_white;
      letter-spacing: -.02em;
      @media (max-width: 1200px) {
        display: inline-block;
        padding-right: 10px;
      }
      @media (max-width: 700px) {
        font-size: 16px;
        padding-right: 0;
      }
      &:not(:last-child) {
        margin-right: 32px;
        @media (max-width: 1200px) {
          margin-right: 0;
        }
      }
      &s {
        display: none;
        align-items: center;
        @media (max-width: 1200px) {
          margin-bottom: 12px;
          display: grid;
          grid-template-columns: repeat(2, 1fr);
        }
        @media (max-width: 700px) {
          margin-bottom: 14px;
          grid-gap: 0 7px;
        }
      }
    }
    &-text {
      color: $color_gray;
      span:not(.shown) {
        display: none;
      }
      @media (max-width: 1200px) {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        span {
          padding-right: 10px;
          &:not(.shown) {
            display: inline;
          }
        }
      }
      @media (max-width: 700px) {
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 8px 0;
        span {
          padding-right: 0;
        }
      }
    }
  }
  &__creator {
    margin-top: 5px;
    @media (max-width: 1200px) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 0 16px;
      grid-column: span 4;
      margin: 0;
      &-item {
        font-size: 14px;
        &:not(:last-child) {
          margin-bottom: 0;
        }
        span {
          margin: 0;
        }
      }
    }
    @media (max-width: 700px) {
      grid-column: span 1;
      grid-template-columns: repeat(1, 1fr);
      grid-gap: 10px 0;
      &-item {
        font-size: 12px;
      }
    }
    &-item {
      display: inline-block;
      color: $color_gray;
      &:not(:last-child) {
        margin-bottom: 8px;
      }
      span {
        display: inline-block;
        color: $color_white;
        margin: 4px 0 0;
      }
    }
  }
  &__bottom {
    @include grid;
    @media (max-width: 1200px) {
      font-size: 14px;
    }
    @media (max-width: 700px) {
      font-size: 12px;
    }
  }
  &__copy {
    color: $color_gray;
    @media (max-width: 1200px) {
      order: +1;
    }
  }
  &__privacy {
    grid-column: span 5;
    @media (max-width: 1200px) {
      grid-column: span 4;
      margin-bottom: 16px;
    }
    &-description {
      color: $color_gray;
      margin-bottom: 8px;
    }
  }
}