@import "main-bx";

.social {
  display: inline-flex;
  &__item {
    flex-shrink: 0;
    width: 32px;
    height: 32px;
    border-radius: 4px;
    border: 1px solid $color_gray;
    padding: 3px;
    &:not(:last-child) {
      margin-right: 8px;
    }
    svg {
      width: 100%;
      height: 100%;
    }
  }
}