@import "main-bx";

.logo {
  display: inline-block;
  &__img {
    width: 196px;
    margin-bottom: 10px;
    @media (max-width: 1200px) {
      width: 175px;
      margin-bottom: 0;
    }
  }
  &__text {
    color: $color_gray;
    @media (max-width: 1200px) {
      display: none;
    }
  }
}