@import "main-bx";

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  padding: 0 32px;
  border: none;
  border-radius: 4px;
  background-color: $color_white;
  font-size: 14px;
  font-weight: 600;
  color: $color_primary;
  line-height: 1.2;
  @media (max-width: 1200px) {
    width: 100%;
  }
}