@import "main-bx";

*, *::before, *::after {
  box-sizing: border-box;
}

html {
  font-family: $font_base;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  -webkit-animation: bugfix infinite 1s;
  line-height: 1.3;
  letter-spacing: -0.01em;
  margin: 0;
  padding: 0;
}

body {
  font-style: normal;
  font-weight: normal;
  -webkit-animation: bugfix infinite 1s;
  line-height: 1.3;
  letter-spacing: -0.01em;
  margin: 0;
  padding: 0;
  // color: $color_white;
  // background-color: $coor_bg;
}

input, textarea {
  -webkit-animation: bugfix infinite 1s;
  line-height: inherit;
  margin: 0;
  padding: 0;
}

a {
  color: unset;
}

a, a:hover {
  text-decoration: none
}

button, input, a, textarea {
  outline: none;
  cursor: pointer;
  font: inherit;
  &:focus, &:active {
    outline: none
  }
}

h1, h2, h3, h4, h5, h6 {
  font: inherit;
  margin: 0;
  padding: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  width: 100%;
  height: auto;
  display: block;
  // image-rendering: -webkit-optimize-contrast;
}

ul, ol {
  padding: 0;
  margin: 0;
  list-style-type: none;
  li {
    margin: 0;
    padding: 0;
  }
}

.container {
  max-width: 1500px;
  padding: 0 26px;
  margin: 0 auto;
  @media (max-width: 1200px) {
    max-width: 960px;
  }
  @media (max-width: 700px) {
    max-width: 540px;
    padding: 0 8px;
  }
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

:focus {
  outline-style: none;
  outline-width: 0 !important;
  outline-color: none !important;
}